html {
  font: size 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #071A31;
}
body.fix-body {
  overflow: hidden;
}
body.fixed {
  padding-top: 7.7rem;
  left: 0;
  top: 0;
}

p {
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
  color: #B1B1B1;
}

.container {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background: #d53541;
  background: linear-gradient(270deg, #d53541 0%, #260b27 100%);
  padding: 0.5rem;
  z-index: 100;
}
@media (min-width: 768px) {
  .header.fixed {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem rgba(0, 0, 0, 0.3);
  }
}

@media (min-width: 480px) {
  .principal-navegation {
    display: flex;
    max-width: 100%;
    justify-content: space-between;
    margin: 0;
  }
}
.principal-navegation a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  margin: 0 auto;
  padding: 1rem 0;
}

.underline-hover-effect {
  display: inline-block;
  position: relative;
}

.underline-hover-effect::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background-color: #FFFFFF;
  transition: width 0.2s ease-out;
  justify-content: center;
}

.underline-hover-effect:hover::before {
  width: 100%;
}

.video {
  position: relative;
  max-height: 40rem;
  overflow: hidden;
  margin-bottom: 5rem;
}
.video .overlay {
  position: absolute;
  background: #d53541;
  background: linear-gradient(270deg, rgba(213, 53, 65, 0.2) 0%, rgba(38, 11, 39, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.video-content {
  z-index: 10;
  text-align: center;
  color: #FFFFFF;
}
.video-content h1 {
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
.video-content h2 {
  font-size: 5rem;
  margin-bottom: 1rem;
}
.video-content h3 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .video-content {
    padding: 10rem;
  }
  .video-content h1 {
    font-size: 2.4rem;
    margin-bottom: 1rem;
  }
  .video-content h2 {
    font-size: 5rem;
    margin-bottom: 1rem;
  }
  .video-content h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }
}
@media (min-width: 992px) {
  .video-content {
    padding: 10rem;
  }
  .video-content h1 {
    font-size: 3.6rem;
    margin-bottom: 1rem;
  }
  .video-content h2 {
    font-size: 7rem;
    margin-bottom: 1rem;
  }
  .video-content h3 {
    font-size: 2.4rem;
    margin-bottom: 1rem;
  }
}

.festival-information {
  display: grid;
  gap: 2rem;
  margin-bottom: 5rem;
}
@media (min-width: 480px) {
  .festival-information {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .festival-information {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .festival-information {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.information-box {
  background-color: #061323;
  margin: auto;
  padding: 2rem;
}
.information-box .information-icon {
  display: block;
  margin: 0 auto;
}
.information-box .information-title {
  font-size: 1.4rem;
  text-align: center;
  color: #FFFFFF;
}
.information-box .information-line {
  display: block;
  margin: 0 auto;
  max-width: 80%;
  border-color: #D53541;
}
.information-box .information-text {
  font-size: 1rem;
  text-align: center;
}

.newsletter {
  margin-bottom: 5rem;
}
.newsletter .newsletter-title {
  font-size: 3.6rem;
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.newsletter .newsletter-text {
  font-size: 1.4rem;
  text-align: center;
}
.newsletter .newsletter-form {
  display: grid;
  grid-template-columns: 60% 40%;
  column-gap: 2rem;
  align-items: center;
  max-width: 90%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .newsletter .newsletter-form {
    max-width: 50%;
  }
}
.newsletter .newsletter-form input {
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
  border: none;
  border-bottom: 1px solid #FFFFFF;
  font-size: 1.4rem;
  color: #FFFFFF;
  background-color: #061323;
}
.newsletter .newsletter-form button {
  background-color: #D53541;
  border-radius: 8px;
  border: none;
  color: #FFFFFF;
  font-weight: 600;
  letter-spacing: 2.5%;
  padding: 0.5rem;
}

.lineup {
  width: 100%;
}

.lineup-title {
  color: #FFFFFF;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 1rem;
}

.lineup-line {
  display: block;
  margin: 0 auto;
  max-width: 30%;
  height: 2px;
  color: #D53541;
  border-style: solid;
  background-color: #D53541;
  margin-bottom: 2.5rem;
}

.artist-box {
  position: relative;
  width: 100%;
}
.artist-box .artist-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #FFFFFF;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.artist-box img {
  width: 100%;
  height: 300px;
  margin: -1rem 0;
  object-fit: cover;
}
.artist-box .artist-name {
  font-size: 3rem;
  margin: 0;
}
.artist-box .lineup-date {
  font-size: 1.2rem;
  color: #CCCCCC;
  margin: 0.5rem;
}
.artist-box .lineup-stage {
  font-size: 1.2rem;
  color: #CCCCCC;
  margin: 0.5rem;
}
@media (min-width: 480px) {
  .artist-box {
    width: 100%;
  }
  .artist-box img {
    width: 100%;
    height: 300px;
    object-fit: cover;
  }
  .artist-box .artist-name {
    font-size: 4rem;
  }
  .artist-box .lineup-date {
    font-size: 1.5rem;
  }
  .artist-box .lineup-stage {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  .artist-box {
    width: 100%;
  }
  .artist-box img {
    width: 100%;
    height: 400px;
    margin: -1rem 0;
    object-fit: cover;
  }
  .artist-box .artist-name {
    font-size: 6rem;
    margin: 0;
  }
  .artist-box .lineup-date {
    font-size: 2rem;
    color: #CCCCCC;
    margin: 1rem;
  }
  .artist-box .lineup-stage {
    font-size: 2rem;
    color: #CCCCCC;
    margin: 1rem;
  }
}
@media (min-width: 992px) {
  .artist-box {
    width: 100%;
  }
  .artist-box img {
    width: 100%;
    height: 500px;
    margin: -1rem 0;
    object-fit: cover;
  }
  .artist-box .artist-name {
    font-size: 7rem;
    margin: 0;
  }
  .artist-box .lineup-date {
    font-size: 2rem;
    color: #CCCCCC;
    margin: 1rem;
  }
  .artist-box .lineup-stage {
    font-size: 2rem;
    color: #CCCCCC;
    margin: 1rem;
  }
}

.gallery {
  margin-top: 5rem;
}

.gallery-title {
  color: #FFFFFF;
  text-align: center;
  margin: 0 0 1rem 0;
}

.gallery-line {
  display: block;
  margin: 0 auto;
  max-width: 30%;
  height: 2px;
  color: #D53541;
  border-style: solid;
  background-color: #D53541;
  margin-bottom: 2.5rem;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0;
    margin-right: -2rem;
  }
}
.gallery-images .gallery-image {
  height: 100px;
  width: 100px;
  cursor: pointer;
}
@media (min-width: 992px) {
  .gallery-images .gallery-image {
    height: 200px;
    width: 200px;
    margin: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}
.overlay .overlay-image {
  max-width: 10em;
  max-height: 10rem;
  margin: auto;
  object-fit: cover;
}
@media (min-width: 768px) {
  .overlay .overlay-image {
    max-width: 30em;
    max-height: 30rem;
  }
}

.close-button {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.close-button:hover {
  cursor: pointer;
}

.tickets {
  margin: 0 auto;
  margin-bottom: 5rem;
}
.tickets .tickets-title {
  color: #FFFFFF;
  margin: 0 0 1rem 0;
  text-align: center;
}
.tickets .tickets-line {
  display: block;
  margin: 0 auto;
  max-width: 30%;
  height: 2px;
  color: #D53541;
  border-style: solid;
  background-color: #D53541;
  margin-bottom: 2.5rem;
}
.tickets .tickets-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .tickets .tickets-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    align-items: center;
  }
}

.ticket {
  margin: 0 auto;
  border-radius: 0.8rem;
  border: solid 2px #B1B1B1;
  padding: 0 1rem 1rem 1rem;
  text-align: center;
  min-width: 17rem;
}
@media (min-width: 768px) {
  .ticket {
    min-width: 14rem;
  }
}
@media (min-width: 992px) {
  .ticket {
    min-width: 17rem;
  }
}
.ticket:hover {
  border: solid 2px #FFFFFF;
}
.ticket-header .ticket-title {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin: 1rem;
}
.ticket-header .ticket-price {
  color: #FFFFFF;
  background-color: rgba(177, 177, 177, 0.2);
  margin: 0 -1rem 1rem;
  padding: 0.5rem;
}
.ticket-body {
  color: #B1B1B1;
}
.ticket-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 1rem;
  margin-bottom: 1rem;
}
.ticket-body ul li {
  padding: 0.2rem;
}
.ticket-body ul hr {
  color: #B1B1B1;
  border-style: solid;
  background-color: #B1B1B1;
}
.ticket-footer .ticket-button {
  background-color: rgba(255, 255, 255, 0);
  color: #FFFFFF;
  border: solid 2px #FFFFFF;
  border-radius: 5rem;
  padding: 0.6rem 1.2rem;
  font-size: 1.2rem;
  margin-top: 0.5rem;
}
.ticket-footer .ticket-button:hover {
  background-color: #FFFFFF;
  color: #000000;
  transition: all 0.2s ease-in-out;
  transform: scale(1.1);
}

.footer {
  background: #d53541;
  background: linear-gradient(270deg, #d53541 0%, #260b27 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  align-items: center;
}
.footer .footer-title {
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  font-weight: bold;
  margin: 0;
  padding: 0.5rem 0;
}
.footer .footer-icons {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  padding: 0.5rem 0;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1rem;
  }
  .footer .footer-title {
    padding: 0;
  }
  .footer .footer-icons {
    padding: 0;
  }
}