@use 'base/variables' as v;

html {
    font: size 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: v.$principalFont;
    font-size: 1.6rem;
    background-color: v.$blue;

    &.fix-body {
        overflow: hidden;
    }

    &.fixed {
        padding-top: 7.7rem;
        left: 0;
        top: 0;
    }
}

p {
    font-family: v.$secondaryFont;
    font-size: 2rem;
    color: v.$grey;
}

.container {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}